.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.product-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.product-card .MuiCardContent-root {
  flex-grow: 1;
}

.product-card .MuiCardMedia-root {
  height: 200px;
  background-size: contain;
}

.cart-item {
  display: flex;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.cart-item-details {
  flex-grow: 1;
  padding: 0 20px;
}

.cart-summary {
  margin-top: 20px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 4px;
}
